export const mutations ={
    //修改vuex的state数据 data载荷  data.key修改数据名称 data.value修改的数据
    changeData(state,data){
        state[data.key] = data.value;
    },
    //修改商品数据 { isHot:[ ] }
    changeProducts(state,data){
        //data.type类型   data.value商品数据 关联对象rightProductsState
        state.productsState[data.type] = data.value; // isHot = [ ]

    }
}