import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//导入font-awesome字体图标库 安装npm install less less-loader css-loader style-loader file-loader font-awesome --save
import 'font-awesome/css/font-awesome.min.css'
import './assets/css/icons.css'
// 安装swiper：npm i swiper -S
import 'swiper/swiper-bundle.css'
//使用vant组件，安装cnpm i vant -S 和 cnpm i babel-plugin-import -D 然后在babel.config.js配置plugins
//导入vant框架 或者 全局import vant from "vant"; import "vant/lib/index.css";
//时间过滤器： 安装cnpm i moment -S
import moment from "moment";
Vue.filter("dateFormat",function (dateStr) {
  return moment(dateStr).format("YYYY-MM-DD hh:mm:ss");
});

import {
  NavBar,
  Form,
  Field,
  Button,
  Toast,
  Tabbar, 
  TabbarItem,
  Swipe, 
  SwipeItem,
  Empty, 
  Lazyload,
  Stepper,
  GoodsAction,
  goodsActionIcon,
  goodsActionButton,
  SwipeCell,
  Cell, 
  CellGroup,
  Checkbox,
  SubmitBar,
  AddressEdit,
  List,
  AddressList,
  ContactCard,
  Popup,
  Tabs,
  Tab,
  Icon,
  Uploader,
  ShareSheet,
  Search,
  NoticeBar,
  Dialog,
} from 'vant'

//导入用于设置 rem 基准值模块可以把px自动转为rem 安装cnpm i postcss-pxtorem lib-flexible -D  然后新建文件夹postcss.config.js配置
import 'lib-flexible'

//注册vant框架的组件 或者 全局Vue.use(vant);
// Vue.use(Vant);
Vue
  .use(NavBar)
  .use(Form)
  .use(Field)
  .use(Button)
  .use(Toast)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Swipe)
  .use(SwipeItem)
  .use(Lazyload)
  .use(Empty)
  .use(Stepper)
  .use(GoodsAction)
  .use(goodsActionIcon)
  .use(goodsActionButton)
  .use(SwipeCell)
  .use(Cell)
  .use(CellGroup)
  .use(Checkbox)
  .use(SubmitBar)
  .use(AddressEdit)
  .use(List)
  .use(AddressList)
  .use(ContactCard)
  .use(Popup)
  .use(Tabs)
  .use(Tab)
  .use(Icon)
  .use(Uploader)
  .use(ShareSheet)
  .use(Search)
  .use(NoticeBar)
  .use(Dialog)
  
//1.导入axios：安装npm i axios vue-axios --save  导入import axios from 'axios'和 import VueAxios from 'vue-axios' 引入使用 Vue.use(VueAxios, axios)
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios) //注册axios
//2.配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8'
//3.发起请求前的处理
axios.interceptors.request.use(config => {
  
  if(config.method == 'post'){
    let paramsString ='';
    for(let key in config.data){//拼接 id=11&name=xxx
      paramsString += key + '=' + config.data[key]+ '&'
    }

    

    config.data = paramsString.slice(0, -1);//去掉最后一个&后面

    
    
  }
  return config;
})
//4.添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';
//5.配置请求基础路径 可以在register.vue界面的url:'/register'
axios.defaults.baseURL = 'http://www.kangliuyong.com:10002'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
