export const routes = [
  {
    path: '/register',
    name: 'Register',
    component: r => require(['../views/Register.vue'], r)
  },
  {
    path: '/login',
    name: 'Login',
    component: r => require(['../views/Login.vue'], r)
  },

  {
    path: '/main',
    name: 'Main',
    component: r => require(['../views/Main.vue'], r),
    children: [
      {
        path: 'home',
        name: 'Home',
        component: r => require(['../views/main/Home.vue'], r)
      },
      {
        path: 'menu',
        name: 'Menu',
        component: r => require(['../views/main/menu.vue'], r),
      },
      {
        path: 'cart',
        name: 'Cart',
        component: r => require(['../views/main/cart.vue'], r),
      },
      {
        path: 'my',
        name: 'My',
        component: r => require(['../views/main/my.vue'], r),
      },

    ]
  },
  {
    path: 'order',
    name: 'Order',
    component: r => require(['../views/order.vue'], r),
  },
  {
    // 参数aid可有可无。所以加？
    path: '/address/:aid?',
    name: 'Address',
    component: r => require(['../views/Address.vue'], r)
  },
  {
    path: '/myaddress',//收货地址
    name: 'Myaddress',
    component: r => require(['../components/address/Myaddress.vue'], r)
  },
  {
    path: '/details',
    name: 'Details',
    component: r => require(['../views/Details.vue'], r)
  },
  {
    path: '/pay',
    name: 'Pay',
    component: r => require(['../views/Pay.vue'], r)
  },
  {
    path: '/myAccount',
    name: 'myAccount',
    component: r => require(['../components/manage/myAccount.vue'], r)
  },
  {
    path: '/mycollect',
    name: 'myCollect',
    component: r => require(['../components/manage/myCollect.vue'], r)
  },
  {
    path: '/myLists',
    name: 'myLists',
    component: r => require(['../components/manage/myLists.vue'], r)
  },
  {
    path: '/myAaddress',
    name: 'myAaddress',
    component: r => require(['../components/manage/myAaddress.vue'], r)
  },
  {
    path: '/mysecure',
    name: 'mySecure',
    component: r => require(['../components/manage/mySecure.vue'], r)
  },
  {
    path: '/guide',
    name: 'guide',
    component: r => require(['../components/publics/guide.vue'], r)
  },
  {
    path: '/adv',
    name: 'adv',
    component: r => require(['../components/publics/advertise.vue'], r),

    beforeEnter: (to, from, next) => {
      
      if (localStorage.advertise) {
        next();
      } else {
        localStorage.advertise = true;
        next('/guide')
      }
    }
  },
  {
    path: '/search',
    name: 'Search',
    component: r => require(['../components/search/search.vue'], r)
  },
  {
    // path: '*',
    // redirect: {
    //   name: 'adv'
    // }
    path: '/',
    redirect: '/adv'
  },
]